import React, { useEffect } from "react";

// Leaflet library
import L from "leaflet";
import { Marker, Popup, TileLayer, useMapEvents } from "react-leaflet";

// Assets
import styles from "./Map.module.css";

const Map = (props) => {
  const { latitude, longitude, zoom } = props.position;

  // Current ID active element
  const activeID = props.currentID;

  // Array markers
  const markersList = props.markesList;

  // Creating a hook for listening to events
  const mapEvent = useMapEvents({});

  // Update position map camera
  useEffect(() => {
    mapEvent.setView([latitude, longitude], zoom);
  }, [latitude, longitude]);

  // Icon list item
  const defaultIcon = L.icon({
    iconUrl:
      "data:image/png;base64,",
  });

  const activeIcon = L.icon({
    iconUrl:
      "data:image/png;base64,",
    className: styles.active,
  });
  // ------------------

  // Output markers on the map
  const markers = markersList.map(({ id, latitude, longitude, name }) => {
    return (
      <Marker
        key={id}
        icon={id === activeID ? activeIcon : defaultIcon}
        position={[latitude, longitude]}
      >
        <Popup>{`Здесь находится машина ${name}`}</Popup>
      </Marker>
    );
  });

  return (
    <>
      <TileLayer
        attribution='&copy; <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
        url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
      />
      {markers}
    </>
  );
};

export default Map;
